








































































































































































































































import {
  Component,
  Mixins
} from 'vue-property-decorator'
import chain from "@/mixins/chain";
import {
  getActivityList,
  getActivitySetting
} from "@/api/modules/chain";

@Component({})
export default class Chain extends Mixins(chain) {

  name = "chain";

  /** 获取活动详情 */
  activityDetail = {}

  /** 活动商品数组 */
  activityList = []

  /** 活动信息 */
  countDetail = {}
  /** 倒计时 */
  timeData = {}
  /** 倒计时状态 */
  state = ""

  /** 规格弹窗 */
  showVisible = false

  /** 规格操作暂存对象 */
  currentItem = {}

  /** 商品总价 */
  totalPrice = 0 || '0'

  /** 选择商品总数 */
  goodNum = 0

  /** 用户购买记录 */
  buyQuery = {}

  /** 参与接龙 */
  joinList = []

  page = 1
  size = 10
  /** 是否有更多数据 */
  hasMore = true

  /** 是否有分页数据 */
  hasListMore = true

  /** 商品分页数据 */
  goodQuery = {
    page: 1,
    size: 10,
  }

  /** 是否有授权 */
  authType = false

  /** 跳转携带参数 */
  options = {}

  actId = ""

  hasChainAct = false

  bottomType = true

  bottomHeight = 46

  // 组件周期函数--监听组件挂载完毕
  mounted() {
    uni.setNavigationBarTitle({
      title: "接龙",
    });
    this.getActivitySetting();
    this.getPosition(0);
    this.getHeight();
  }

  /**
   * 获取接龙活动开关
   */
  getActivitySetting() {
    getActivitySetting({}).then(res => {
      this.hasChainAct = Boolean(res.activityState)
      if (this.hasChainAct) {
        this.getActivityContent();
      }
    });
  }

  getActivityContent() {
    getActivityList({})
      .then(res => {
        this.actId = res.id;
        this.getActivity(res.id);
      })
      .catch(() => {
        this.hasChainAct = false
      });
  }

  onReachBottom() {
    if (this.hasListMore) {
      this.goodQuery.page = this.goodQuery.page + 1
      this.getListDetail();
    }
  }
}
